/*MAIN*/
body{
    margin: 0;
    padding: 0;
    background-color: #fff;
    
}
.heading{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 900px;
    font-size: 50px;
    color: rgb(0, 255, 0);
}
hr{
    background: rgb(66, 66, 66);
}
.message{
    margin-top: 5%;
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    color: rgb(0, 255, 0);
}
.signin-text{
    font-size: 22px;
}
.btn{
    color: #fff;
    background: #000000;
    border: 2px solid rgb(0, 255, 0);
}
.btn:hover{
    background: rgb(0, 255, 0);
    color: #000000;
    box-shadow: 1px 1px 25px rgb(0, 255, 0);
}
#img-grid{
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 765px) {
    
    #img-grid{
        display: none;
        margin: 0;
        padding: 0;
    }
}
